"use client";

import React, { useLayoutEffect, useRef } from "react";
import { gsap } from "gsap";
import DemoWithFounder from "../../../public/svg/DemoWithFounder";
import CommandCenter from "./CommandCenter";

export default function HeroSection() {
  const sectionRef = useRef<HTMLElement | null>(null);
  const splitRef = useRef<{ chars?: HTMLElement[]; revert?: () => void } | null>(null);
  const mmRef = useRef<any>(null);

  useLayoutEffect(() => {
    // create context scoped to the section element
    const ctx = gsap.context(() => {
      const mm = gsap.matchMedia();
      mmRef.current = mm;

      // single media rule that always runs; you can add different queries if needed
      mm.add("(min-width: 0px)", () => {
        const headingEl = sectionRef.current?.querySelector(".hero-heading") as HTMLElement | null;
        if (!headingEl) return;

        // store original HTML so we can fully revert DOM on cleanup
        const originalHTML = headingEl.innerHTML;

        // robust fallback: wrap only TEXT nodes (preserve any child elements like the gradient span)
        const chars: HTMLElement[] = [];
        const wrapTextNodes = (node: Node) => {
          const snapshot = Array.from(node.childNodes);
          for (const child of snapshot) {
            if (child.nodeType === Node.TEXT_NODE) {
              const text = child.nodeValue ?? "";
              const frag = document.createDocumentFragment();
              for (const ch of text) {
                const span = document.createElement("span");
                // classes must match your tailwind usage; inline-block is required for transforms
                span.className = "reveal-char inline-block";
                span.textContent = ch === " " ? "\u00A0" : ch; // preserve spaces
                frag.appendChild(span);
                chars.push(span);
              }
              child.parentNode?.replaceChild(frag, child);
            } else if (child.nodeType === Node.ELEMENT_NODE) {
              // recurse into elements (preserves element structure like <span class="...">)
              wrapTextNodes(child);
            }
          }
        };

        wrapTextNodes(headingEl);

        // store for cleanup
        splitRef.current = {
          chars,
          revert: () => {
            if (headingEl) headingEl.innerHTML = originalHTML;
          },
        };

        // make sure heading isn't hidden by Tailwind initial state
        gsap.set(headingEl, { opacity: 1 });

        // initial state for characters
        gsap.set(chars, { opacity: 0, y: 10, filter: "blur(10px)", scale: 1.2 });

        const tl = gsap.timeline();
        tl.to(chars, {
          duration: 0.6,
          opacity: 1,
          y: 0,
          filter: "blur(0px)",
          scale: 1,
          stagger: 0.06,
          ease: "power3.out",
        });

        tl.fromTo(
          sectionRef.current!.querySelectorAll(".blur-fade"),
          { opacity: 0, y: 10, filter: "blur(8px)" },
          { opacity: 1, y: 0, filter: "blur(0px)", stagger: 0.08, duration: 0.6 },
          "-=0.35"
        );

        // return a kill function for matchMedia re-evaluations
        return () => {
          tl.kill();
          if (splitRef.current?.revert) splitRef.current.revert();
        };
      });
    }, sectionRef.current);

    return () => {
      if (mmRef.current) mmRef.current.revert();
      if (splitRef.current?.revert) splitRef.current.revert();
      ctx.revert();
    };
  }, []);

  return (
    <div ref={sectionRef} className="space-y-[100px]">
      <section className="flex flex-col items-center justify-center text-center pt-[120px] px-3">
        {/* ensure you don't have opacity-0 on this element in your Tailwind classes */}
        <h1 className="hero-heading text-4xl sm:text-5xl md:text-6xl font-bold text-white leading-tight">
          World’s First  <br />
          <span className="bg-gradient-to-b from-[#A4A0FF] to-[#6CE0FF] bg-clip-text text-transparent">
           World’s First AI DevOps Engineer
          </span>
        </h1>

        <p className="mt-6 md:max-w-[40%] text-white/50 text-base sm:text-lg blur-fade">
          From creation to operation at scale, everything becomes faster,
          simpler, more predictable, and at a fraction of the cost.
        </p>

        <div className="mt-10 blur-fade">
          <button className="flex items-center gap-3 text-[12px] p-1 pl-3 rounded-full border border-gray-600 bg-transparent text-white font-bold hover:bg-white/10 transition">
            DEMO WITH FOUNDER
            <DemoWithFounder />
          </button>
        </div>
      </section>

      <CommandCenter />
    </div>
  );
}
